export default "<template>\n  <div>\n    <button @click=\"increase\" :class=\"$style.button\">\n      Add\n    </button>\n    Hi, {{ message }}\n  </div>\n</template>\n\n<script>\nexport default {\n  props: ['name', 'separator'],\n  data() {\n    return {\n      count: 1\n    }\n  },\n  methods: {\n    increase() {\n      this.count++;\n    }\n  },\n  computed: {\n    message() {\n      const { count, separator, name } = this;\n      return Array(count).fill(name).join(separator)\n    }\n  }\n};\n</script>\n\n<style module>\n.button {\n  color: red;\n}\n</style>\n";